body {
  background-color: #1d8ef4;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.Container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-left: 3em;
  padding-right: 3em;
  height: 100vh;
}

@media (max-width: 930px) {
  .Container {
    flex-direction: column;
  }
}

.innerCard {
  flex: 33%;
  text-align: center;
  background-color: #ffffff;
  border-radius: 24px;
  margin: 2em;
  font-weight: lighter;
}

.innerCard:hover {
  transform: translateY(-6px);
  transition: ease-in 0.1s;
}

.mode {
  color: rgb(169, 169, 169);
  margin-bottom: 0.2rem;
}

.amount {
  margin-top: 0;
  font-size: 36px;
}

.month {
  font-weight: lighter;
  font-size: 15px;
}

.details {
  font-size: 20px;
  text-align: start;
  padding-left: 1.5em;
}

.detailLine {
  padding-bottom: 0.5em;
}

.btn {
  height: 3.5em;
  width: 90%;
  margin: 1em;
  padding: 1em;
  background-color: #569afe;
  color: #ffffff;
  font-size: 16px;
  border: 0px;
  border-radius: 28px;
  cursor: pointer;
}

.btn:hover {
  background-color: #3585fd;
}
